import React from 'react';
import BanesYaml from '../core-rules/banes/banes.yml';
import SingleItemBane from '../components/banes/single-item-bane';
import NavLayout from '../layouts/nav-layout';

export default function SingleBaneTemplate({ location }) {
  const baneSlug  = location.pathname.replace(/\/bane\//, '')
  const bane = BanesYaml.filter(item => item.name.toLowerCase().replace(/\s+/, '-') === baneSlug)[0]
  console.log('data', baneSlug)
  return (
    <div>
      <NavLayout>
        <SingleItemBane bane={bane} />
      </NavLayout>
    </div>
  )
}

